import _ from 'lodash';

const roundDist = (distribution) => {
  return distribution.map((value) => {
    return parseInt(value * 10000) / 100;
  });
}

export const getConfidenceNumbers = (jsonFile) => {

  const variations = jsonFile[0].trafficAllocation.length - 1;
  const confidences = [
    {
      pValues: Array(variations).fill(1),
      confidenceIntervals: Array(variations).fill([-Infinity, Infinity]),
    }
  ];

  jsonFile.forEach((visitor) => {
    confidences.push({
      pValues: Object.values(visitor.stats).map((statObject) => statObject.pValue),
      confidenceIntervals: Object.values(visitor.stats).map((statObject) => statObject.confInterval),
    });
  });

  return confidences;
};

export const getCounts = (jsonFile) => {
  const variations = jsonFile[0].trafficAllocation.length;
  const visitorCounts = [{
    conversions: Array(variations).fill(0),
    visitors: Array(variations).fill(0),
  }];

  jsonFile.forEach((visitor) => {
    const summaryObject = _.cloneDeep(visitorCounts[visitorCounts.length - 1]); 
    summaryObject.visitors[visitor.variation] = summaryObject.visitors[visitor.variation] + 1;
    if (visitor.convert) {
      summaryObject.conversions[visitor.variation] = summaryObject.conversions[visitor.variation] + 1;
    }
    visitorCounts.push(summaryObject);
  });

  return visitorCounts;
};

export const getDistributions = (jsonFile, sampleRate = 1) => {
  const variations = Array(jsonFile[0].trafficAllocation.length).fill([]);
  const initialPoints = roundDist(_.cloneDeep(jsonFile[0].trafficAllocation));
  const distributions = [];
  variations.forEach((variation, index) => {
    distributions[index] = [initialPoints[index]];
  });

  jsonFile.forEach((visitor, visitorIndex) => {
    const roundedDist = roundDist(visitor.trafficAllocation);
    variations.forEach((variation, index) => {
      if (!(visitorIndex % sampleRate)) {
        distributions[index].push(roundedDist[index]);
      }
    });
  });

  return distributions;
};

export default {
  getConfidenceNumbers,
  getCounts,
  getDistributions,
};
